<template>
    <div class="add-message">
        <el-form ref="form"
                 :model="form"
                 label-width="120px"
                 size="mini">
            <el-form-item label="类型">
                <el-radio-group v-model="type"
                                size="medium"
                                @change="change">
                    <el-radio border
                              label="1">文本消息</el-radio>
                    <el-radio border
                              label="0">图文消息</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form ref="subForm"
                     :model="subForm"
                     label-width="120px"
                     size="mini"
                     v-if="type == 1 ? false : true">
                <el-form-item label="图片"
                              prop="imageFile">
                    <el-upload :show-file-list="false"
                               enctype="multipart/form-data"
                               name="imageFile"
                               action="http://crm.rrdtjj.top/message/upload"
                               :on-success="handleAvatarSuccess">
                        <el-button size="small"
                                   type="primary">点击上传</el-button>
                        <img v-if="imgUrl" :src="imgUrl" alt="" width="200">
                    </el-upload>
                </el-form-item>
                <el-form-item label="标题">
                    <el-input v-model="subForm.title"
                              style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item label="链接">
                    <el-input v-model="subForm.url"
                              style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item label="详细信息"
                              prop="description">
                    <el-input type="textarea"
                              v-model="subForm.description"
                              style="width:500px;"></el-input>
                </el-form-item>
                <el-form-item size="large">
                    <el-button type="primary"
                               @click="addOne">添加</el-button>
                </el-form-item>
                <el-form-item label="图文消息"
                              prop="pic_message">
                    <el-table :data="picMessageData"
                              style="width: 100%;"
                              border>
                        <el-table-column prop="title"
                                         label="标题"
                                         width="100"></el-table-column>
                        <el-table-column prop="picurl"
                                         label="图片"
                                         width="200">
                            <template slot-scope="scope">
                                <img :src="scope.row.picurl" width="100%">
                            </template>
                        </el-table-column>
                        <el-table-column prop="url"
                                         label="链接"
                                         width="200"></el-table-column>
                        <el-table-column prop="description"
                                         label="详细信息"
                                         width="300"></el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">
                                <el-button size="small"
                                           type="success"
                                           class="send-message"
                                           @click="deleteMessage(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-form>
            <el-form-item v-else
                          label="详细信息"
                          prop="description">
                <el-input type="textarea"
                          v-model="description"
                          style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="人工/延时/定时">
                <el-radio-group v-model="channel"
                                size="medium">
                    <el-radio border
                              label="0">人工</el-radio>
                    <el-radio border
                              label="1">延时</el-radio>
                    <el-radio border
                              label="2">定时</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item v-if="channel == '1'"
                          label="延时时间(分钟)">
                <el-input v-model="delay"
                          style="width:100px;"></el-input>
            </el-form-item>
            <el-form-item v-if="channel == '2'"
                          label="定时时间">
                <el-date-picker v-model="timing_time"
                                type="datetime"
                                placeholder="选择日期时间"></el-date-picker>
            </el-form-item>
            <el-form-item label="选择标签"
                          prop="tagName">
                <div class="tag-name">
                    <el-tag closable
                            @close="handleClose()"
                            size="mini"
                            v-model="tagName"
                            v-if="tagName">{{tagName}}</el-tag>
                </div>
            </el-form-item>
            <el-form-item v-if="tagList != []">
                <ul class="tag-list">
                    <li v-for="(item, index) in tagList"
                        :key="index"
                        @click="select(item)">
                        <el-tag>{{item.name}}</el-tag>
                    </li>
                </ul>
            </el-form-item>
            <el-form-item label="是否接管">
                <el-switch v-model="flag"
                           @change="change1"></el-switch>
            </el-form-item>
            <el-form-item label="公号"
                          prop="codes1"
                          v-if="flag">
                <el-checkbox-group v-model="codes1">
                    <el-checkbox v-for="(item, index) in gonghaoList1"
                                 :key="index"
                                 :label="item.code"
                                 name="codes1">{{item.name}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="公号"
                          prop="codes2"
                          v-else>
                <el-checkbox-group v-model="codes2">
                    <el-checkbox v-for="(item, index) in gonghaoList2"
                                 :key="index"
                                 :label="item.code"
                                 name="codes2">{{item.name}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item size="large">
                <el-button type="primary"
                           @click="onCreate">立即创建</el-button>
                <el-button @click="cancel">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Axios from "axios";
    export default {
      data() {
        return {
          form: {},
          type: "1",
          subForm: {},
          picMessageData: [],
          codes: [],
          codes1: [],
          codes2: [],
          delay: "",
          description: "",
          picMessageSwitch: false,
          imgUrl: "",
          gonghaoList1: [],
          gonghaoList2: [],
          contentData: {},
          contents: [],
          flag: true,
          channel: "0",
          timing_time: "",
          tagName: "",
          tagList: [],
          tagId: ""
        };
      },
      mounted() {
        this.get_codes();
        this.showTagList();
      },
      methods: {
        handleClose() {
          this.tagName = "";
        },
        showTagList() {
          Axios({
            url: "/tag",
            method: "get"
          }).then(res => {
            this.tagList = res.data.data;
          });
        },
        select(item) {
          this.tagId = item._id;
          this.tagName = item.name;
        },
        onCreate() {
          if (this.type == "1") {
            this.contentData.description = this.description;
            this.contents.push(this.contentData);
          } else {
            this.contents = this.picMessageData;
          }
          if (this.flag == true) {
            this.codes = this.codes1;
          } else {
            this.codes = this.codes2;
          }
          Axios({
            url: "/message/create",
            method: "post",
            data: {
              contents: this.contents,
              type: this.type,
              task: this.channel == "1" ? true : false,
              is_timing: this.channel == "2" ? true : false,
              delay: this.delay,
              timing_time: this.timing_time == "" ? "" : this.timing_time,
              codes: this.codes,
              img: this.contents[0].picurl,
              take_over: this.flag,
              tagId: this.tagId
            }
          }).then(res => {
            if (res.data.success) {
              this.$message({
                type: "success",
                message: res.data.success
              });
              window.location.href = "/msg_view/";
              this.form = {};
              this.description = "";
              this.picMessageData = this.codes = [];
            } else {
              this.$message.error(res.data.err);
            }
          });
        },
        cancel() {
          this.$router.push("/msg_view");
        },
        addOne() {
          this.subForm.picurl = this.imgUrl;
          this.picMessageData.push(this.subForm);
          this.subForm = {};
          this.imgUrl = "";
        },
        handleAvatarSuccess(res, file) {
          this.imgUrl = "http://crm.rrdtjj.top/uploads/" + res.filename;
        },
        change() {
          this.form = {};
        },
        change1() {
          this.codes1 = this.codes2 = [];
        },
        deleteMessage(index, row) {
          this.picMessageData.splice(index, 1);
        },
        get_codes() {
          Axios({
            url: "/message/get_code",
            method: "get",
            params: {}
          }).then(res => {
            if (res.data.codes != "") {
              let arr1 = [];
              let arr2 = [];
              for (let i = 0; i < res.data.codes.length; i++) {
                let index = i;
                if (res.data.codes[index].take_over == 1) {
                  arr1.push(res.data.codes[index]);
                } else {
                  arr2.push(res.data.codes[index]);
                }
              }
              this.gonghaoList1 = arr1;
              this.gonghaoList2 = arr2;
            }
          });
        }
      }
    };
</script>

<style scoped>
    .add-message {
      border: 1px solid #eee;
      padding: 20px;
    }
    .tag-name {
      width: 100px;
      height: 30px;
      line-height: 30px;
      border: 1px solid #eee;
      border-radius: 5px;
      padding-left: 10px;
    }
    .tag-list li {
      float: left;
      margin-right: 10px;
      cursor: pointer;
    }
</style>




